<template>
  <div>
    <v-header>
      <div slot="right" :class="['h-right',times.length > 0 ? '' : 'opa-6']" v-if="type > 0">
        <a @click="setTime" class="mg-r10">确定</a></div>
    </v-header>

    <v-content :type="'choose'">
      <ul class="timeList">
        <li v-if="times.length == 0">
          <p class="title">选择时段</p>
          <div class="edit" @click="doAdd">
            <p class="text none">请选择时段</p>
            <span class="img transfer"></span>
          </div>
        </li>
        <li v-for="item in times">
          <p class="title">时段{{$index + 1 | n2c-filter}}</p>
          <div class="edit">
            <p class="text" @click="edit($index)">{{item.start}}-{{item.end}}</p>
            <span class="img del" @click="del($index)"></span>
          </div>
        </li>
      </ul>
      <div class="point" v-show="times.length > 0 && times.length < 3">
        <p class="color-main title" @click="doAdd"><span class="img add"></span>添加多个时段</p>
        <p class="font-12 color-9 help">每次发布最多添加3个不同时段</p>
      </div>
    </v-content>

    <div :class="['time-box','z-index-max', adding ? 'active':'']">
      <div class="warp">
        <div class="top">
          <div class="ps-abs mg-l10 font-14" @click="cancel">取消</div>
          <div class="font-16">选择时段</div>
          <div class="ps-abs mg-r10 font-14 t-right" @click="add">确定</div>
        </div>
        <div class="dis-flex font-12 mid">
          <div class="flex-1">
            <p>开始时间</p>
            <p>{{start}}</p>
          </div>
          <div class="flex-1">
            <p>结束时间</p>
            <p>{{end}}</p>
          </div>
        </div>
        <div class="box-selector">
          <div class="box-mark"></div>
          <div class="dis-flex boxes">
            <div id="hour" class="flex-1">
              <ul>
                <li>&nbsp;</li>
                <li v-for="value in hours" :class="value == hour ? 'active' : ''">
                  {{value | zero-fill}}点
                </li>
                <li>&nbsp;</li>
              </ul>
            </div>
            <div id="minute" class="flex-1">
              <ul>
                <li>&nbsp;</li>
                <li v-for="value in minutes" :class="value == minute ? 'active' : ''">
                  {{value | zero-fill}}分
                </li>
                <li>&nbsp;</li>
              </ul>
            </div>
            <div id="after" class="flex-2">
              <ul>
                <li>&nbsp;</li>
                <li v-for="value in afters" :class="value == after ? 'active' : ''">
                  {{value}}小时后结束
                </li>
                <li>&nbsp;</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import $ from 'jquery'
  import {iScroll} from '../../assets/iscroll/iscroll'
  import {publish, order} from '../../const'
  import {toast} from '../../assets/js/toast'
  import {zeroFill} from  '../../filter'

  export default{
    route: {
      data({from, to}){
        if (this.isReady) {
          return {
            state: !this.state
          }
        }
      }
    },
    ready(){
      this.loadQuery();
      this.init();
      this.isReady = true;
    },
    data(){
      return {
        isReady: false,
        state: false,
        times: [],//{start: '', end: ''}
        type: 0,
        targetType: 0,
        workTime: '',
        adding: false,
        start: '08:30',
        end: '09:30',
        hour: 8,
        minute: 30,
        after: 1,
        time: {start: '', end: ''},
        hours: [],
        minutes: [],
        afters: [],
        isReset: [true, true, true],//滑动重置
        sObj: [],//滚动对象
        filters: {starts: [], ends: []},
        editIndex: -1//-1为新增
      }
    },
    methods: {
      loadQuery(){
        let query = this.$route.query;
        this.targetType = query.targetType;
        if (query.type && query.type > 0) {
          this.type = query.type;
          let p = this.targetType == 1 ? publish.get(this.type) : order.get(this.type);//区分发布还是协议
          if (!p) {
            if (this.targetType == 1) {
              window.location.replace('#/publish');
            } else {
              window.location.replace('#/order');
            }
            return;
          }
          this.workTime = p.workTime;
          if (this.workTime) {
            let times = this.workTime.split(' ');
            let time;
            for (let i in times) {
              time = times[i].split('-');
              this.times.push({
                start: time[0],
                end: time[1]
              });
            }
          }
        } else {
          if (this.targetType == 1) {
            window.location.replace('#/publish');
          } else {
            window.location.replace('#/order');
          }
          return;
        }
      },
      init(){
        let vm = this;
        setTimeout(function () {
          vm.loadScroll();
          window.addEventListener("DOMContentLoaded", vm.loadScroll, false);
        }, 100);
      },
      doAdd(){
        this.editIndex = -1;
        this.$emit('init-time');
      },
      del(index){
        this.times.splice(index, 1);
      },
      edit(index){
        this.editIndex = index;
        this.$emit('init-time', index);
      },
      timeMove(){

      },
      cancel(){
        this.adding = false;
        toast.hide_mask();
      },
      add(){
        let index = this.editIndex;
        console.log(`editIndex:${index} time:${this.start},${this.end}`);
        if (index == -1) {
          this.times.push({
            start: this.start,
            end: this.end
          });
        } else {
          this.times[index] = {
            start: this.start,
            end: this.end
          };
          this.times.push();
        }
        this.cancel();
      },
      loadScroll(){
        let vm = this;
        let s1 = new iScroll("hour", {
          hScroll: false, vScrollbar: false,
          onScrollEnd: function () {
            if (!vm.isReset[0] && vm.adding) {//重置
              vm.getSelect(s1, '#hour', 0);
            } else {
              vm.isReset[0] = false;
            }
          }
        });
        let s2 = new iScroll("minute", {
          hScroll: false, vScrollbar: false,
          onScrollEnd: function () {
            if (!vm.isReset[1] && vm.adding) {//重置
              vm.getSelect(s2, '#minute', 1);
            } else {
              vm.isReset[1] = false;
            }
          }
        });
        let s3 = new iScroll("after", {
          hScroll: false, vScrollbar: false,
          onScrollEnd: function () {
            if (!vm.isReset[2] && vm.adding) {//重置
              vm.getSelect(s3, '#after', 2);
            } else {
              vm.isReset[2] = false;
            }
          }
        });
        vm.sObj = [s1, s2, s3];//赋值
        //销毁实例
        vm.$once('destroy-scroll', function () {
          s1.destroy();
          s2.destroy();
          s3.destroy();
        });
        vm.$on('init-time', function (index) {
          console.log(`打开盒子:${index}`);
          vm.checkTime(index);//校验可选时间
          let y1 = vm.getTimeIndex(vm.hours, vm.hour) * 45 * -1;
          let y2 = vm.getTimeIndex(vm.minutes, vm.minute) * 45 * -1;
          let y3 = vm.getTimeIndex(vm.afters, vm.after) * 45 * -1;
          vm.isReset = [true, true, true];
          vm.refsAll();
          s1.scrollTo(0, y1, 100, false);
          s2.scrollTo(0, y2, 100, false);
          s3.scrollTo(0, y3, 100, false);
        });
      },
      getSelect(s, id, index){//scrollEnd 回调 scroll id 操作数据向
        let mark = $(".box-selector .box-mark").offset().top;
        let top = $(id + " li:first-child").offset().top;
        let plus = parseFloat((mark - top) % 45);
        let n = parseInt((mark - top) / 45);//n为改元素的index
        let before = $(id + " li.active").index() - 1;//上个被选中的元素index
        if (n == 1 && plus < 15) {
          n = 0;
        } else {
          n = n - 1;
        }
        if (n >= before) {//上个选中元素的下面
          if (plus > 15) {
            n = n + 1;
          }
        }
        n = n < 0 ? 0 : n;
//        console.log(`${mark},${top},${plus},${n},${before}`);
        if (index == 0) {
          if (n == this.hours.length) {
            n = n - 1;
          }
          this.hour = this.hours[n];
        } else if (index == 1) {
          if (n == this.minutes.length) {
            n = n - 1;
          }
          this.minute = this.minutes[n];
        } else {
          if (n == this.afters.length) {
            n = n - 1;
          }
          this.after = this.afters[n];
        }
//        console.log(`改变值:${index}`);
        //重置改变后的数据
        this.scrollChange(index);

        this.isReset[index] = true;//已重置
        s.scrollTo(0, (n * 45 * -1), 10, false);
        this.setSelectTime();
      },
      setSelectTime(){
        this.start = zeroFill(this.hour) + ":" + zeroFill(this.minute);
        let min = this.hour * 60 + this.minute + this.after * 60;
        let h = parseInt(min / 60);
        let m = min % 60;
        this.end = zeroFill(h) + ":" + zeroFill(m);
      },
      getTimeIndex(items, item){
        for (let i in items) {
          if (items[i] == item) {
            return parseInt(i);
          }
        }
      },
      getTimePlus(start, end){
        let s = parseInt(start.split(':')[0]) * 60 + parseInt(start.split(':')[1]);
        let e = parseInt(end.split(':')[0]) * 60 + parseInt(end.split(':')[1]);
        return parseFloat((e - s) / 60);
      },
      checkTime(index){
        let vm = this;
        //先填充满
        vm.hours = [];
        vm.minutes = [];
        vm.afters = [];
        for (let i = 0; i < 24; i++) {
          vm.hours.push(i);
        }
        for (let i = 1; i <= 24; i += 0.5) {
          vm.afters.push(i);
        }
        for (let i = 0; i < 6; i++) {
          vm.minutes.push(i * 10);
        }
        //第一次选择时直接选择
        if (index == undefined && vm.times.length == 0) {
          if (!this.hour) {
            this.hour = vm.hours[0];
            this.after = vm.afters[0];
            this.minute = vm.minutes[0];
          }
          vm.scrollChange();
          vm.setSelectTime();
          vm.checkOk();
          return;
          //只有一个时 不进入过滤
        } else if (index == 0 && vm.times.length == 1) {
          let time = vm.times[index];
          console.log(`修改时间:${time.start},${time.end}`);
          let start = time.start;
          vm.hour = parseInt(start.split(':')[0]);
          vm.minute = parseInt(start.split(':')[1]);
          vm.after = vm.getTimePlus(time.start, time.end);
          vm.start = time.start;
          vm.end = time.end;

          vm.scrollChange();
          vm.setSelectTime();
          vm.checkOk();
          return;
        }
        let filters = [];
        //已选时间过滤
        if (index != undefined && vm.times.length > 1) {
          let times = vm.times;
          for (let i = 0; i < times.length; i++) {
            if (index != i) {
              filters.push(times[i]);
            }
          }
          //新增时添加所有
        } else {
          filters = vm.times;
        }

        vm.filters = {starts: [], ends: []};
        if (filters.length > 0) {
          let time, plus, s, e;
          for (let i = 0; i < filters.length; i++) {
            time = filters[i];
            plus = vm.getTimePlus(time.start, time.end);
            s = parseInt(time.start.split(':')[0]);//开始的小时数
            e = parseInt(time.end.split(':')[0]);//结束的小时数

            vm.filters.starts.push({h: s, m: parseInt(time.start.split(':')[1])});
            vm.filters.ends.push({h: e, m: parseInt(time.end.split(':')[1])});
            //计算要舍去的小时
            if (parseInt(time.start.split(':')[1]) == 0 && s != 0) {//开始时间为整数时间时减去上一个小时
              s = s - 1;
            }
            if (parseInt(time.end.split(':')[1]) == 50 || e == 23)//结束时间为该时间的50分时 则去除该时间 或结束时间在23点
            {
              e = e + 1;
            }
            for (let j = s; j < e; j++) {
              vm.hours.splice(vm.getTimeIndex(vm.hours, j), 1);//删除元素
            }
          }
        }
//        console.log(`filters:${JSON.stringify(vm.filters)}`);
        if (index == undefined) {//初始化时间
          vm.hour = vm.hours[0];
          if (vm.hour == undefined) {
            toast.alert('已无剩余可选时间');
            return;
          }
          vm.minute = vm.minutes[0];
          vm.after = vm.afters[0];
        } else {
          let time = vm.times[index];
//          console.log(`修改时间:${time.start},${time.end}`);
          let start = time.start;
          vm.hour = parseInt(start.split(':')[0]);
          vm.minute = parseInt(start.split(':')[1]);
          vm.after = vm.getTimePlus(time.start, time.end);
          vm.start = time.start;
          vm.end = time.end;
        }

        vm.scrollChange();
        vm.setSelectTime();
        vm.checkOk();
      },
      checkOk(){
        $("#hour ul").css({height: parseFloat(this.hours.length * 0.45 + 0.9) + 'rem'});
        $("#minute ul").css({height: parseFloat(this.minutes.length * 0.45 + 0.9) + 'rem'});
        $("#after ul").css({height: parseFloat(this.afters.length * 0.45 + 0.9) + 'rem'});
        toast.mask(null, true);
        this.adding = true;
      },
      scrollChange(index){
        let h = this.hours;
        let _index = this.getTimeIndex(h, this.hour);//当前时间在数组中的位置
        let len = 1;
        let end = 0;
        for (let i = (_index + 1); i < h.length; i++) {
          if (h[i] != (h[i - 1] + 1)) {//下个值间隔时 则最大时间为前一个值
            end = h[i];//间隔的值
            break;
          }
          len++;
        }
//        console.log(`可选时间:${h},可选时间区:${len}`);
        let startMin = -1;
        //当然时间+1 下个过滤的时间  min < filter.starts - 10
        let startTime = this.inFilters(this.hour + 1, true);
        if (len == 1 && startTime) {
          startMin = startTime.m;
        }
        //当前时间 in filters.ends  min > filter.ends + 10
        let endTime = this.inFilters(this.hour, false);
        let endMin = -1;
        if (endTime) {
          endMin = endTime.m;
        }

        let _minutes = [];
        let _afters = [];
        if (startMin != -1) {//下一个时间为过滤的 开始时间
          console.log(`下个时间为过滤时间 ${startMin}`);
          len = 1;//剩余时间为1小时
          for (let i = 0; i < startMin; i += 10) {
            _minutes.push(parseInt(i));
          }
        } else if (endMin != -1) {//本时间为过滤的 结束时间
          console.log(`本时间为过滤结束时间 ${endMin}`);
          for (let i = (endMin + 10); i < 60; i += 10) {
            _minutes.push(parseInt(i));
          }
        } else {//该时间不在任何区间
          _minutes = [0, 10, 20, 30, 40, 50];
        }

        let nextEnd;
        //寻找下一个过滤的时间
        if (startMin == -1) {
//          console.log(`寻找下一个过滤的开始时间 h=> ${_index}+${len}`);
          let nextStart = this.inFilters(h[_index] + len, true);
          if (!nextStart) {//如果没找到  则寻找被kill掉的时间
            nextStart = this.inFilters(h[_index] + len + 1, true);
          }
          if (!nextStart) {//上个start时间是00分
            nextStart = this.inFilters(h[_index] + len + 2, true);
          }
          if (!nextStart) {//还是没有说明是最终时间
            nextEnd = '24:00';
          } else {
            console.log(`下个已选时段${nextStart.h}:${nextStart.m}`);
            if (nextStart.m == 0) {//分钟为0时 递减到上个时段
              nextEnd = zeroFill((nextStart.h - 1)) + ':50';
            } else {
              nextEnd = zeroFill(nextStart.h) + ':' + zeroFill((nextStart.m - 10));
            }
          }
          console.log(`下个结束时间:${nextEnd}`);
          //计算 afters
          let plus = this.getTimePlus(zeroFill(this.hour) + ':' + zeroFill(this.minute), nextEnd);
          if (plus % 1 != 0 && plus % 1 != 0.5) {
            if (plus % 1 > 0.5) {
              len = parseInt(plus) + 0.5;
            } else {
              len = parseInt(plus);
            }
          } else {
            len = plus;
          }
          if (len < 1) {//时间差小于1小时 则改变分
            len = 1;
          }
        }
        for (let i = 1; i <= len; i += 0.5) {
          _afters.push(i);
        }

        //本段不为过滤的 手动调整
        if (startMin == -1) {
          if (len == 1) {
            if (index == 0) {
              _minutes = [0];
            } else if (index == 2 && this.hour == 23) {
              _minutes = [0];
            }
          }
        }
        //计算剩余时间
        if (this.adding) {
          if (index == 0) {//小时
            this.minutes = _minutes;
            this.afters = _afters;
            $("#minute ul").css({height: parseFloat(this.minutes.length * 0.45 + 0.9) + 'rem'});
            $("#after ul").css({height: parseFloat(this.afters.length * 0.45 + 0.9) + 'rem'});
            this.minute = this.minutes[0];
            this.resizeUl(1);

            if ($.inArray(this.after, this.afters) < 0) {
              this.after = this.afters[0];
              this.resizeUl(2);
            }
            this.sObj[1].refresh();
            this.sObj[2].refresh();
          } else if (index == 1) {//分钟
            this.afters = _afters;
            $("#after ul").css({height: parseFloat(this.afters.length * 0.45 + 0.9) + 'rem'});
            if ($.inArray(this.after, this.afters) < 0) {
              this.after = this.afters[0];
              this.resizeUl(2);
            }
            this.sObj[2].refresh();
          } else {//小时后
            this.minutes = _minutes;
            $("#minute ul").css({height: parseFloat(this.minutes.length * 0.45 + 0.9) + 'rem'});
            if ($.inArray(this.minute, this.minutes) < 0) {
              this.minute = this.minutes[0];
              this.resizeUl(1);
            }
            this.sObj[1].refresh();
          }
        } else {
          this.minutes = _minutes;
          this.afters = _afters;
          if (this.editIndex == -1) {//新增值则进行赋值
            this.minute = this.minutes[0];
            this.after = this.afters[0];
          }
        }
      },
      refsAll()
      {
        let scrolls = this.sObj;
        for (let i = 0; i < scrolls.length; i++) {
          scrolls[i].refresh();
        }
      },
      resizeUl(index){
        let y = 0;
        if (index == 1) {
          y = this.getTimeIndex(this.minutes, this.minute);
        } else if (index == 2) {
          y = this.getTimeIndex(this.afters, this.after);
        } else {
          y = this.getTimeIndex(this.hours, this.hour);
        }
        console.log(`resizeUl index:${index} 所在位置的Y倍:${y}`)
        this.sObj[index].scrollTo(0, y * 45 * -1, 100, false);
      },
      inFilters(h, start)
      {
        let filter;
        if (start) {
          filter = this.filters.starts;
        } else {
          filter = this.filters.ends;
        }
        for (let i in filter) {
          if (filter[i].h == h) {
            return filter[i];
          }
        }
        return null;
      },
      setTime(){
        if (this.times.length == 0) {
          return;
        }
        let p = this.targetType == 1 ? publish.get(this.type) : order.get(this.type);//区分发布还是协议
        let times = this.times;
        let timeLines = [];
        for (let i = 0; i < times.length; i++) {
          timeLines.push(times[i].start + "-" + times[i].end);
        }
        p.workTime = timeLines.join(' ');
        console.log(`设置工作时间 ${p.workTime}`);
        //区分发布还是协议
        if (this.targetType == 1) {
          publish.set(p, this.type);
        } else {
          order.set(p, this.type);
        }
        window.history.back();
      }
    },
    watch: {
      'state': function () {
        this.times = [];//{start: '', end: ''}
        this.type = 0;
        this.workTime = '';
        this.adding = false;
        this.start = '08:30';
        this.end = '09:30';
        this.hour = 8;
        this.minute = 30;
        this.after = 1;
        this.time = {start: '', end: ''};
        this.isReset = [true, true, true];
        this.loadQuery();
      }
    },
    destroyed()
    {
      this.$emit('destroy-scroll');
    }
    ,
    components: {
      VHeader,
      VContent
    }
  }
</script>
<style scoped>
  .timeList {
    background-color: #fff;
    border-bottom: .01rem solid #e5e5e5;
    font-size: .14rem;
    text-align: left;
  }

  .timeList li {
    border-bottom: .01rem solid #e5e5e5;
    position: relative;
    height: .45rem;
    margin-left: .1rem;
    padding-right: .1rem;
    line-height: .45rem;
  }

  .timeList li:last-child {
    border-bottom: none;
  }

  li .title {
    float: left;
    width: 25%;
  }

  li .edit {
    float: left;
    width: 70%;
  }

  .edit .text {
    color: #333;
  }

  .edit .text.none {
    color: #999;
  }

  .img.transfer {
    position: absolute;
    right: .1rem;
    top: .15rem;
    width: .08rem;
    height: .12rem;
    background-size: .08rem;
    background-image: url(../../assets/img/icon/arrow-right.png);
  }

  .img.del, .img.write {
    position: absolute;
    top: .1rem;
    width: .22rem;
    height: .22rem;
    background-size: .22rem;
  }

  .img.del {
    right: .12rem;
    background-image: url(../../assets/img/icon/del.png);
  }

  .img.write {
    right: .12rem;
    background-image: url(../../assets/img/icon/edit.png);
  }

  .point {
    margin-top: .2rem;
    text-align: center;
  }

  .point .title {
    font-size: .14rem;
    text-indent: .2rem;
    position: relative;
    margin: 0 auto;
    width: 1.2rem;
  }

  .point .help {
    margin-top: .05rem;
  }

  .img.add {
    position: absolute;
    top: 0;
    left: 0;
    width: .19rem;
    height: .19rem;
    background-size: .19rem;
    background-image: url(../../assets/img/icon/add.png);
  }

  .time-box {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 0;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
  }

  .time-box.active {
    height: 2.44rem;
  }

  .time-box .warp {
    height: 100%;
    text-align: center;
  }

  .time-box .top {
    height: .44rem;
    line-height: .44rem;
    background-color: #3c506b;
    color: #fff;
    text-align: center;
  }

  .top .t-right {
    right: 0;
    top: 0;
  }

  .time-box .mid {
    height: .5rem;
    background-color: #f3f3eb;
    color: #fa3c46;
  }

  .time-box .box-selector {
    background-color: #fff;
    height: 1.55rem;
    position: relative;
  }

  .box-selector .box-mark {
    position: absolute;
    top: .55rem;
    left: 0;
    right: 0;
    height: .45rem;
    border-top: .01rem solid #e5e5e5;
    border-bottom: .01rem solid #e5e5e5;
  }

  .box-selector .boxes {
    height: 100%;
    position: relative;
  }

  .boxes > div {
    position: absolute;
    top: .1rem;
    bottom: .1rem;
    left: 0;
    width: 25%;
    overflow: hidden;
  }

  .boxes > div:nth-child(2) {
    left: 25%;
  }

  #after {
    left: 50%;
    width: 50%;
  }

  .boxes ul {
    color: #999;
    height: 100%;
    z-index: 9999;
  }

  .boxes li {
    height: .45rem;
    line-height: .45rem;
  }

  .boxes li.active {
    color: #03c5a3;
  }
</style>




